@import "../node_modules/tinper-bee-core/scss/minxin-variables";
@import "../node_modules/tinper-bee-core/scss/minxin-mixins";
// @import "../node_modules/bee-checkbox/src/Checkbox";
// $loading: url('./assets/img/tree-loading.gif');
$loading:url('data:image/gif;base64,');

$treePrefixCls : "u-tree";

//css 分割线


.u-tree {
  margin: 0;
  padding: 5px;
  font-size: 12px;
  outline: none;
  .u-tree-checkbox {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: middle;
    height: 16px;
  }
  .u-tree-checkbox-checked .u-tree-checkbox-inner, .u-tree-checkbox-indeterminate .u-tree-checkbox-inner {
      // background-color: $tree-checkbox-color;
      // border-color: $tree-checkbox-color;
  }
  .u-tree-checkbox-inner {
      position: relative;
      top: 0;
      left: 0;
      display: inline-block;
      width: 16px;
      height: 16px;
      border: 1px solid $border-color-base;
      border-radius: 3px;
      background-color: #fff;
      -webkit-transition: all .3s;
      transition: all .3s;
      box-sizing: border-box;
  }
  .u-tree-checkbox-checked .u-tree-checkbox-inner, .u-tree-checkbox-indeterminate .u-tree-checkbox-inner {
      // background-color: $tree-checkbox-color;
      // border-color: $tree-checkbox-color;
  }
  .u-tree-checkbox-disabled .u-tree-checkbox-inner {
      border-color: #dfe1e6;
      background-color: #fff;
  }
  .u-tree-checkbox-indeterminate .u-tree-checkbox-inner:after {
      content: ' ';
      -webkit-transform: scale(1);
      -ms-transform: scale(1);
      transform: scale(1);
      position: absolute;
      left: 2px;
      top: 5px;
      width: 8px;
      height: 1px;
  }
  .u-tree-checkbox-disabled.u-tree-checkbox-checked .u-tree-checkbox-inner:after {
      -webkit-animation-name: none;
      animation-name: none;
      border-color: #c1c7d0;
  }
  .u-tree-checkbox-disabled .u-tree-checkbox-inner:after {
      -webkit-animation-name: none;
      animation-name: none;
      border-color: #c1c7d0;
  }
  .u-tree-checkbox-checked .u-tree-checkbox-inner:after {
      -webkit-transform: rotate(45deg) scale(1);
      -ms-transform: rotate(45deg) scale(1);
      transform: rotate(45deg) scale(1);
      position: absolute;
      left: 4px;
      top: 1px;
      display: table;
      width: 5px;
      height: 8px;
      border: 2px solid $tree-checkbox-color;
      border-top: 0;
      border-left: 0;
      content: ' ';
      -webkit-transition: all 0.2s cubic-bezier(0.12, 0.4, 0.29, 1.46) 0.1s;
      transition: all 0.2s cubic-bezier(0.12, 0.4, 0.29, 1.46) 0.1s;
  }
  .u-tree-checkbox-inner:after {
      -webkit-transform: rotate(45deg) scale(0);
      -ms-transform: rotate(45deg) scale(0);
      transform: rotate(45deg) scale(0);
      position: absolute;
      left: 4px;
      top: 1px;
      display: table;
      width: 5px;
      height: 8px;
      border: 2px solid $tree-checkbox-color;
      border-top: 0;
      border-left: 0;
      content: ' ';
      -webkit-transition: all 0.1s cubic-bezier(0.71, -0.46, 0.88, 0.6);
      transition: all 0.1s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  }
  li span.u-tree-checkbox {
    margin: 2px 4px 0 0;
  }
  li {
    padding: 0;
    margin: 2px 0;
    list-style: none;
    white-space: nowrap;
    outline: 0;
    // &:first-child {
    //   padding-top: 8px;
    // }
    &:last-child {
      padding-bottom: 0;
  }
  }
  li a[draggable],
  li a[draggable="true"] {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    /* Required to make elements draggable in old WebKit */
    -khtml-user-drag: element;
    -webkit-user-drag: element;
  }
  li.drag-over > a[draggable] {
    background-color: #108ee9;
    color: white;
    opacity: 0.8;
  }
  li.drag-over-gap-top > a[draggable] {
    border-top: 2px #108ee9 solid;
  }
  li.drag-over-gap-bottom > a[draggable] {
    border-bottom: 2px #108ee9 solid;
  }
  li.filter-node > a {
    color: #f50;
    font-weight: bold;
  }
  li ul {
    margin: 0;
    padding: 0 0 0 18px;
  }
  li a {
    display: inline-block;
    padding: 3px 4px;
    border-radius: 2px;
    margin: 0;
    cursor: pointer;
    text-decoration: none;
    vertical-align: middle;
    color: $font-color-base;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    // a 标签hover宽度调整
   &.u-tree-node-content-wrapper {
      width: calc(100% - 18px);
    }
   
  }
  li a:hover {
    background-color: $tree-node-bg-color;
  }
  li a.u-tree-node-selected {
    background-color: $selected-bg-color-base;
    .u-tree-title{
      color: $brand-primary;
    }
  }
  li span.u-checkbox {
    margin: 2px 4px 0 0;
  }
  li span.u-tree-switcher,
  li span.u-tree-iconEle {
    margin: 0;
    width: 22px;
    height: 22px;
    line-height: 22px;
    display: inline-block;
    vertical-align: middle;
    border: 0 none;
    cursor: pointer;
    outline: none;
    i.uf{
      padding: 0px;
    }
    + .u-tree-title{
      margin-left: 2px;
    }
  }
  li span.u-tree-icon_loading:after {
    display: inline-block;
    // margin-right: 2px;
    vertical-align: middle;
    background: $loading no-repeat scroll 0 0 transparent;
    // text-rendering: optimizeLegibility;
    // -webkit-font-smoothing: antialiased;
    // -moz-osx-font-smoothing: grayscale;
    content: "";
    // -webkit-animation: loadingCircle 1s infinite linear;
    //         animation: loadingCircle 1s infinite linear;
     width: 14px;
     height: 14px;
  }
  li span.u-tree-switcher.u-tree-switcher-noop {
    cursor: auto;
  }
  li span.u-tree-switcher.u-tree-roots_open,
  li span.u-tree-switcher.u-tree-center_open,
  li span.u-tree-switcher.u-tree-bottom_open,
  li span.u-tree-switcher.u-tree-noline_open {
    position: relative;
  }
  li span.u-tree-switcher.u-tree-roots_open:after,
  li span.u-tree-switcher.u-tree-center_open:after,
  li span.u-tree-switcher.u-tree-bottom_open:after,
  li span.u-tree-switcher.u-tree-noline_open:after {
    font-size: 18px;
    font-size: 7px \9;
    -webkit-transform: scale(0.58333333) rotate(0deg);
        -ms-transform: scale(0.58333333) rotate(0deg);
            transform: scale(0.58333333) rotate(0deg);
    /* IE6-IE8 */
    -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(sizingMethod='auto expand', M11=1, M12=0, M21=0, M22=1)";
    zoom: 1;
    display: inline-block;
    font-family: 'uf';
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\e639";
    font-weight: bold;
    position: absolute;
    top: 0;
    // right: 4px;
    right: 0px;
    left: 0;
    color: #666;
    -webkit-transition: -webkit-transform .3s ease;
    transition: -webkit-transform .3s ease;
    transition: transform .3s ease;
    transition: transform .3s ease, -webkit-transform .3s ease;
  }
  .u-tree-child-tree {
    display: none;
  }
  .u-tree-child-tree-open {
    display: block;
  }
  .u-tree-treenode-disabled > span,
  .u-tree-treenode-disabled > a,
  .u-tree-treenode-disabled > a span {
    color: #909090;
    cursor: not-allowed;
    &:hover{
      background: transparent;
    }
  }
  .u-tree-icon__open {
    margin-right: 2px;
    vertical-align: top;
  }
  .u-tree-icon__close {
    margin-right: 2px;
    vertical-align: top;
  }
  .u-motion-collapse {
    overflow: hidden;
  }
  .u-motion-collapse-active {
    -webkit-transition: height 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: height 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
}

:root .u-tree li span.u-tree-switcher.u-tree-roots_open:after,
:root .u-tree li span.u-tree-switcher.u-tree-center_open:after,
:root .u-tree li span.u-tree-switcher.u-tree-bottom_open:after,
:root .u-tree li span.u-tree-switcher.u-tree-noline_open:after {
  -webkit-filter: none;
          filter: none;
}
:root .u-tree li span.u-tree-switcher.u-tree-roots_open:after,
:root .u-tree li span.u-tree-switcher.u-tree-center_open:after,
:root .u-tree li span.u-tree-switcher.u-tree-bottom_open:after,
:root .u-tree li span.u-tree-switcher.u-tree-noline_open:after {
  font-size: 18px;
}
.u-tree li span.u-tree-switcher.u-tree-roots_close,
.u-tree li span.u-tree-switcher.u-tree-center_close,
.u-tree li span.u-tree-switcher.u-tree-bottom_close,
.u-tree li span.u-tree-switcher.u-tree-noline_close {
  position: relative;
  -ms-filter: "progid:DXImageTransform.Microsoft.BasicImage(rotation=3)";
}
.u-tree li span.u-tree-switcher.u-tree-roots_close:after,
.u-tree li span.u-tree-switcher.u-tree-center_close:after,
.u-tree li span.u-tree-switcher.u-tree-bottom_close:after,
.u-tree li span.u-tree-switcher.u-tree-noline_close:after {
  font-size: 18px;
  font-size: 7px \9;
  -webkit-transform: scale(0.58333333) rotate(0deg);
      -ms-transform: scale(0.58333333) rotate(0deg);
          transform: scale(0.58333333) rotate(0deg);
  /* IE6-IE8 */
  -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(sizingMethod='auto expand', M11=1, M12=0, M21=0, M22=1)";
  zoom: 1;
  display: inline-block;
  font-family: 'uf';
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\e639";
  font-weight: bold;
  position: absolute;
  top: 0;
  right: 4px;
  color: #666;
  -webkit-transition: -webkit-transform .3s ease;
  transition: -webkit-transform .3s ease;
  transition: transform .3s ease;
  transition: transform .3s ease, -webkit-transform .3s ease;
}
:root .u-tree li span.u-tree-switcher.u-tree-roots_close:after,
:root .u-tree li span.u-tree-switcher.u-tree-center_close:after,
:root .u-tree li span.u-tree-switcher.u-tree-bottom_close:after,
:root .u-tree li span.u-tree-switcher.u-tree-noline_close:after {
  -webkit-filter: none;
          filter: none;
}
:root .u-tree li span.u-tree-switcher.u-tree-roots_close:after,
:root .u-tree li span.u-tree-switcher.u-tree-center_close:after,
:root .u-tree li span.u-tree-switcher.u-tree-bottom_close:after,
:root .u-tree li span.u-tree-switcher.u-tree-noline_close:after {
  font-size: 18px;
}
.u-tree li span.u-tree-switcher.u-tree-roots_close:after,
.u-tree li span.u-tree-switcher.u-tree-center_close:after,
.u-tree li span.u-tree-switcher.u-tree-bottom_close:after,
.u-tree li span.u-tree-switcher.u-tree-noline_close:after {
  -webkit-transform: rotate(270deg) scale(0.6);
      -ms-transform: rotate(270deg) scale(0.6);
          transform: rotate(270deg) scale(0.6);
}

/**
 * 自定义switcher图标
 */

.u-tree li span.u-tree-switcher.icon-none {
  
  &:after{
    content:"";
  }
  .uf {
    padding: 0px;
    font-size: 14px;
  }
}


//line Tree
//
.u-tree {
  &.u-tree-show-line {
    li:not(:last-child) {
      > ul {
        background: url('data:image/gif;base64,R0lGODlhCQACAIAAAMzMzP///yH5BAEAAAEALAAAAAAJAAIAAAIEjI9pUAA7') 0 0 repeat-y;
      }
      > .u-tree-switcher-noop {
        background-position: -56px -18px;
        background-image: url('data:image/png;base64,');
      }
    }
    li:last-child {
      > .u-tree-switcher-noop {
        background-position: -56px -36px;
        background-image: url('data:image/png;base64,');
      }
    }
  }

  :focus {
      outline: none;
  }
}
 